    'teamAdvisersPartners#64': 'J\'ai auparavant t fondateur et associ chez Archimedes Labs  Palo Alto, Californie. J\'adore travailler avec les fondateurs, les quipes, les concepteurs et les ingnieurs pour crer des produits, raconter des histoires et les transformer en ralit. J\'ai fond ou co-fond de nombreuses entreprises depuis le dbut des annes 1980. Certaines ont t vendues avec succs et d\'autres introduites en bourse.',
    'teamAdvisersPartners#65': 'Deux de mes entreprises taient des licornes, qui ont t values  plus d\'un milliard de dollars. J\'ai toujours mis l\'accent sur le moment o le changement se produit. Dans les annes 1980, rseautage et bases de donnes. Au cours de la priode 1994-1998, accs  Internet. De 1998  2010, services et contenus Web, applications mobiles grand public et depuis 2017 blockchain.',
    'teamAdvisersPartners#66': 'Carrire et projets: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs et CINDX',
