    'teamAdvisersPartners#71': 'PDG d\'une entreprise de services financiers prouve avec des rsultats avrs, je mets un accent particulier sur la cration d\'entreprises d\'assurance dans les marchs en dveloppement o j\'ai fond avec succs des entreprises avec une valuation actuelle de plus de 500millions de dollars.',
    'teamAdvisersPartners#72': 'Rcemment en Russie (10ans), ainsi que d\'autres postes de haute direction non lis  l\'assurance, y compris celui de PDG de Workle, la plus grande plateforme de vente sur Internet en Russie, et un large ventail de postes de niveau C dans les start-ups de fintech, dont Crypterium, la premire banque de crypto-monnaie numrique au monde.',
    'teamAdvisersPartners#73': 'Carrire et projets: Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex et CINDX',
    'teamAdvisersPartners#74': 'Philip Staehelin',
