    'teamAdvisersPartners#79': 'J\'ai su tirer parti de ma capacit  voir les choses diffremment et  faire avancer les choses dans les environnements d\'entreprise, de conseil et de start-up. Tout cela m\'offre de nouvelles perspectives qui alimentent de nouvelles ides transformatrices.',
    'teamAdvisersPartners#80': 'Trouver des solutions  des problmes difficiles, c\'est ce qui me motive  me lever le matin.',
