    'roadmap#3': `lk olarak CINDX platformu iin bir temel oluturuyoruz. Bu aamada, Minimum Kullanlabilir rn gelitiriliyor. MK, takas ve oaltma protokolne eriim salayacak. Kaynaklarmzn byk bir ksm, platformun leklenebilirliine ve gvenliine ayrlmtr.`,
    'roadmap#4': `Platformun CINDX.alpha srm, bir blokzinciri mimarisi ve akll szlemeler iin temel bir yerleim sistemi ierir. CINDX.market ve CINDX.trade bileenleri de ek ilevler kazanp baz deiikliklerden geer.`,
    'roadmap#5': `CINDX.alpha srmndeki blokzinciri mimarisine dayanarak, kullanclarn iletiim kurmalarn ve faydal ierikler renip dl kazanmalarn salayan CINDX.talk bileenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu nc taraf hizmetlerine sunmak iin, platformun CINDX.core bileenine bir analitik modl eklenecektir.`,
    'roadmap#6': `Bu aamada, CINDX.market ve CINDX.trade bileenlerini ekliyoruz. Yneticiler endeks bazl rnler ekleyebilir ve terminal esnek bir risk ynetimi sistemi ile entegre olur.`,
    'roadmap#7': `CINDX.talk, herkese ak ve zel iletiim kanallarna sahip olur. Bu aamada, ierikler birbirinden ayrlabilir.`,
    'roadmap#8': `Bu aamada platform byk oranda gncellenir. Kullanclar kripto para birimleri ile fiat para birimlerini depolayp takas edebilmeye balar. CINDX.trade, sipari yrtme ve risk ynetiminin iin son derece esnek bir sistem kullanmaya balar. Pazaryeri devreye alnr ancak bu aamada sadece CINDX ve i ortaklar tarafndan gelitirilen uygulamalara yer verilir.`,
    'roadmap#9': `CINDX.talk son srmne jeton bazl bir puanlama sistemi eklenir. Ayrca yeni bir rn tr olan Algoritmik ticaret ve takas ile oklu takas siparileri arasnda otomatik arbitraj ilevi eklenecektir.`,
    'roadmap#10': `lk olarak CINDX platformu iin bir temel oluturuyoruz. Bu aamada, Minimum Kullanlabilir rn gelitiriliyor. MK, takas ve oaltma protokolne eriim salayacak. Kaynaklarmzn byk bir ksm, platformun leklenebilirliine ve gvenliine ayrlmtr.`,
    'roadmap#11': `Platformun CINDX.alpha srm, bir blokzinciri mimarisi ve akll szlemeler iin temel bir yerleim sistemi ierir. CINDX.market ve CINDX.trade bileenleri de ek ilevler kazanp baz deiikliklerden geer.`,
    'roadmap#12': `CINDX.alpha srmndeki blokzinciri mimarisine dayanarak, kullanclarn iletiim kurmalarn ve faydal ierikler renip dl kazanmalarn salayan CINDX.talk bileenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu nc taraf hizmetlerine sunmak iin, platformun CINDX.core bileenine bir analitik modl eklenecektir. Yeni bir CINDX.hub bileeni, CINDX'in salad piyasa verilerini analiz etmek iin uygulama gelitirmeyi salar.`,
    'roadmap#13': `Bu aamada, CINDX.market ve CINDX.trade bileenlerini ekliyoruz. Yneticiler endeks bazl rnler ekleyebilir ve terminal esnek bir risk ynetimi sistemi ile entegre olur. Gelitiriciler iin, CINDX zerinde uygulama gelitirmeyi kolaylatracak bir UGK yaymlanr.`,
    'roadmap#14': `CINDX.talk, herkese ak ve zel iletiim kanallarna sahip olur. Bu aamada, ierik trleri birbirlerinden ayrt edilebilir. CINDX.hub, uygulama gelitirmeyi daha da kolaylatran bir Github entegrasyonuna sahip olur.`,
    'roadmap#15': `Bu aamada platform byk oranda gncellenir. Kullanclar kripto para birimleri ile fiat para birimlerini depolayp takas edebilmeye balar. CINDX.trade, sipari yrtme ve risk ynetiminin iin son derece esnek bir sistem kullanmaya balar. Pazaryeri devreye alnr ancak bu aamada sadece CINDX ve i ortaklar tarafndan gelitirilen uygulamalara yer verilir. CINDX.hub, tarihsel piyasa verilerine dayanan bir test sistemi ile ticaret stratejileri gelitirmek iin tam lekli bir editre sahip olur.`,
    'roadmap#16': `CINDX.talk son srmne jeton bazl bir puanlama sistemi eklenir. Ayrca yeni bir rn tr olan Algoritmik ticaret ve takas ile oklu takas siparileri arasnda otomatik arbitraj ilevi eklenecektir. Kod denetimi iin st dzey bir platform ve CINDX.talk'da jeton bazl derecelendirmeye dayal topluluk uzman deerlendirmesi oluturulur.`,
    'roadmap#17': `lk olarak CINDX platformu iin bir temel oluturuyoruz. Bu aamada, Minimum Kullanlabilir rn gelitiriliyor. MK, takas ve oaltma protokolne eriim salayacak. Kaynaklarmzn byk bir ksm, platformun leklenebilirliine ve gvenliine ayrlmtr.`,
    'roadmap#18': `Platformun CINDX.alpha srm, bir blokzinciri mimarisi ve akll szlemeler iin temel bir yerleim sistemi ierir. CINDX.market ve CINDX.trade bileenleri de ek ilevler kazanp baz deiikliklerden geer.`,
    'roadmap#19': `CINDX.alpha srmndeki blokzinciri mimarisine dayanarak, kullanclarn iletiim kurmalarn ve faydal ierikler renip dl kazanmalarn salayan CINDX.talk bileenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu nc taraf hizmetlerine sunmak iin, platformun CINDX.core bileenine bir analitik modl eklenecektir. Yeni bir CINDX.hub bileeni, CINDX'in salad piyasa verilerini analiz etmek iin uygulama gelitirmeyi salar. Ekip, jeton sahiplerine platformun gelirini datacak bir sistem olan CINDX.dao bileenini gelitirir.`,
    'roadmap#20': `Bu aamada, CINDX.market ve CINDX.trade bileenlerini ekliyoruz. Yneticiler endeks bazl rnler ekleyebilir ve terminal esnek bir risk ynetimi sistemi ile entegre olur. Gelitiriciler iin, CINDX zerinde uygulama gelitirmeyi kolaylatracak bir UGK yaymlanr.`,
    'roadmap#21': `CINDX.talk, herkese ak ve zel iletiim kanallarna sahip olur. Bu aamada, ierik trleri birbirlerinden ayrt edilebilir. CINDX.hub, uygulama gelitirmeyi daha da kolaylatran bir Github entegrasyonuna sahip olur.`,
    'roadmap#22': `Bu aamada platform byk oranda gncellenir. Kullanclar kripto para birimleri ile fiat para birimlerini depolayp takas edebilmeye balar. CINDX.trade, sipari yrtme ve risk ynetiminin iin son derece esnek bir sistem kullanmaya balar. Pazaryeri devreye alnr ancak bu aamada sadece CINDX ve i ortaklar tarafndan gelitirilen uygulamalara yer verilir. CINDX.hub, tarihsel piyasa verilerine dayanan bir test sistemi ile ticaret stratejileri gelitirmek iin tam lekli bir editre sahip olur. CINDX.dao, jeton sahipleri iin bir oy kullanma sistemi elde eder.`,
    'roadmap#23': `CINDX.talk son srmne jeton bazl bir puanlama sistemi eklenir. Ayrca yeni bir rn tr olan Algoritmik ticaret ve takas ile oklu takas siparileri arasnda otomatik arbitraj ilevi eklenecektir. Kod denetimi iin st dzey bir platform ve CINDX.talk'da jeton bazl derecelendirmeye dayal topluluk uzman deerlendirmesi oluturulur. CINDX.dao, CINDXin daha da merkezsiz hale gelmesi iin bir delege sistemi sunmaya balar.`,

