    'teamAdvisersPartners#65': `1980'li yllarndan beri saysz irketi bizzat kurdum veya kuruluuna ortaklk ettim. irketlerimden ikisi "Unicorn" (tek boynuzlu at) olarak nitelendirildi ve insanlar tarafndan 1 milyar dolarn zerinde deer biildi. Hayatm boyunca hep deiimin yaand noktaya odaklandm. 1980'lerdeki a kurulumlar ve veri tabanlar. 1994-1998 arasnda nternet Eriimi. 1998-2010 yllar arasnda nternet Hizmetleri ve erik, Mobil Tketici Uygulamalar ve 2017 ylndan bu yana Blok Zinciri.`,
    'teamAdvisersPartners#66': `Kariyer ve projeler: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs ve CINDX`,
