    'teamAdvisersPartners#70': `Deeri 500 milyon dolara ulaan irketler kurma`,
    'teamAdvisersPartners#71': `Tecrbesi, ald sonularla kantlanm olan bir finans hizmetleri CEO'su olarak gelimekte olan pazarlarda sigorta irketleri kurmaya odaklanm durumdaym. Bugne kadar kurmu olduum irketlerin gncel deeri 500 milyon dolarn zerindedir. Yakn zamanda Rusya'da (10 yl) sigorta sektr harici kdemli ynetim pozisyonlarnda bulundum.`,
    'teamAdvisersPartners#72': `Bunlar arasnda Rusya'nn en byk nternet sat istihdam platformu Workle'nin CEO'luu ve dnyann ilk dijital kripto bankas Crypterium da dhil olmak zere, ok sayda fintech giriiminde C seviyesinde pozisyon da bulunuyor.`,
    'teamAdvisersPartners#73': `Kariyer ve projeler: Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex, CINDX`,
