    'teamAdvisersPartners#78': `Bulac iyimserlii pragmatik gerekilikle birletiren bir lider ve yenilikiyim. Kimsenin grmedii yerlerdeki frsatlar gryorum.`,
    'teamAdvisersPartners#79': `"Farkl grme" ve "gerekletirme" becerilerimi kurumsal, danmanlk ve giriimcilik alanlarnda kullanarak yeni ve dnm odakl fikirler retebileceim yeni perspektifler kazandm.`,
