    'teamAdvisersPartners#124': 'Karriere und Projekte: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation, CINDX',
    'teamAdvisersPartners#125': 'Bogdan Fiedur',
    'teamAdvisersPartners#126': 'Blockchain- und Kryptowhrungsexperte, Investor, Unternehmer',
    'teamAdvisersPartners#127': 'Mitbegrnder von bitJob und Prsident von Blockchainexperts.io',
    'teamAdvisersPartners#128': 'ICO-Berater, ICOBench-Experte',
    'teamAdvisersPartners#129': 'Bogdan Fiedur ist ein Smart-Contract-Entwickler, Krypto-Investor, Unternehmer und Prsident von http://blockchainexperts.io. Er verfgt ber mehr als 20 Jahre Erfahrung im Bereich IT im Aufbau von E-Commerce-Websites und war whrend der letzten drei Jahre in die Blockchain-Entwicklung involviert.',
    'teamAdvisersPartners#130': 'Er ist auch ein ICO-Berater, der bis dato mit 20 erfolgreichen ICOs zusammenarbeitet und stark darin eingebunden ist, Prsentationen und Workshops zu Blockchain und Smart-Contract-Programmierung in seinem Heimatort Winnipeg zu geben.',
    'teamAdvisersPartners#131': 'Bogdan fokussiert sich auf die Blockchain-Branche und bietet Beratung, Consulting-Dienstleistungen und Kontakt zu Blockchain-Experten. Krzlich hat Bogdan Bitjob gegrndet, eine Freelancer-Blockchain-Plattform fr Studenten, und ist als Teammitglied Teil mehrerer ICO-Projekte wie PayPie.com.',
    'teamAdvisersPartners#132': 'Karriere und Projekte: US Blockchain Association, Tuurnt, Trends Project, BiNeuro, Zichain, Graphen Tech, CyClean, Swachhcoin, CINDX',
    'teamAdvisersPartners#133': 'Graham Doggart',
    'teamAdvisersPartners#134': '20 Jahre in der Unternehmensentwicklung, Fintech-Marketing-Compliance',
    'teamAdvisersPartners#135': 'Fhrte Kampagnen im Wert von 100 Mio. USD aus',
    'teamAdvisersPartners#136': 'Berater fr 10+ Blockchain-Projekte in diesem Jahr',
    'teamAdvisersPartners#137': 'Ich bin Vorsitzender eines Stealth-Cryptocurrency-Projekts und seit 2012 Mitbegrnder und leitender Stratege bei Dynamic Abundance, einem Blockchain- und Fintech-Beratungsunternehmen.',
    'teamAdvisersPartners#138': 'Ich spezialisiere mich auf die Strukturierung und Aufmachung von Token-Offerings und habe international an verschiedenen Blockchain-, Kryptowhrungs- und ICO-Projekten gearbeitet.',
