    'teamAdvisersPartners#140': 'Karrieren und Projekte: Stealth Cryptocurrency Project, Dynamic Abundance, ELYSIAN, Fox Trading FX, Profede, Worltdopoly, CINDX',
    'teamAdvisersPartners#141': 'Sadie Hutton',
    'teamAdvisersPartners#142': '25 Jahre im leitenden Management und Management-Consulting',
    'teamAdvisersPartners#143': 'Multimillionen-Dollar-Fundraiser mit Kampagnen im Wert von 75 Mio. GBP',
    'teamAdvisersPartners#144': 'ICO-Berater bei 8 Projekten',
    'teamAdvisersPartners#145': 'Ich bin der Grnder von Dynamic Abundance, einem Blockchain-Beratungsunternehmen.',
    'teamAdvisersPartners#146': 'Ich bin ein Spezialist fr ICO-Analyse, Projektmanagement, alle wichtigen Aspekte des ICO-Prozesses, und ich bevorzuge den praktischen Ansatz. Ich verfge ber gute Fhigkeiten im Unternehmens-Management und -Wachstum und bin dadurch in der Lage, ein Projekt von der Idee bis zur Umsetzung zu begleiten. Ich bin von Natur aus ein Problemlser und nehme mich jedes Projekts, in das ich involviert bin, persnlich an. Ich liebe es zu gewinnen!',
    'teamAdvisersPartners#147': 'Mit ber 20 Jahren Management-Erfahrung habe ich ein starkes Netzwerk aufgebaut und biete aktive Beratung bezglich Finanzen, Tech, Verkauf, Marketing sowie Qualittssicherung fr wichtige Blockchain-Projekte.',
    'teamAdvisersPartners#148': 'Karriere und Projekte: Dynamic Abundance, ELYSIAN, Worldopoly, Confidential ICO, Fox Trading FX, Profede, Market Digital, CINDX',
    'teamAdvisersPartners#149': 'Jason King',
    'teamAdvisersPartners#150': '20 Jahre Erfahrung in der Geschftsfhrung',
    'teamAdvisersPartners#151': 'Fhrte Unternehmensoperationen in 30 Lndern',
    'teamAdvisersPartners#152': 'Verantwortung fr ber 500 Mio. USD an G&V',
    'teamAdvisersPartners#153': 'Fast 20 Jahre lang war Jason Kind Vorstandsmitglied in den Bereichen Technologie, Medien und Telekommunikation und in die Strukturierung und das Aushandeln von Geschftsdeals, das Verhandeln von Gewinnen, den Ausbau von Mrkten und die erfolgreiche Positionierung von Organisationen involviert.',
    'teamAdvisersPartners#154': 'Vieler dieser Herausforderungen haben mich in erfolgreiche Rollen in der Restrukturierung und im Turnaround gefhrt  fhren Sie Ihre Branche an, indem Sie die richtigen Menschen und Teams aufbauen, um die profitabelsten Ziele zu erreichen.',
    'teamAdvisersPartners#155': 'In addition to his interests in private equity and startup companies, Jason has worked in various groups including Deutsche Telekom, Telenor and UPC Liberty Global and Veon.',
    'teamAdvisersPartners#156': 'Jason verfgt ber nahezu grenzenlose Erfahrung in verschiedenen Bereichen, darunter im Business- und Operations-Management, in den Bereichen strategische Planung und Finanzen sowie in der Kryptowhrungs- und Blockchain-Branche.',
    'teamAdvisersPartners#157': 'Motti Peer',
    'teamAdvisersPartners#158': 'Mitbegrnder der preisgekrnten global agierenden PR-Agentur Blonde 2.0',
    'teamAdvisersPartners#159': 'Grnder mehrerer Unternehmen in den Bereichen Sicherheit und Finanzen',
    'teamAdvisersPartners#160': 'Motti ist eine der treibenden Krfte im PR-Bereich fr Blockchain- und Krypto-Start-ups und -VCs.',
    'teamAdvisersPartners#161': 'Motti ist Grnder mehrerer Unternehmen in den Bereichen Sicherheit und Finanzen. Er verfgt ber mehrjhrige Erfahrung als Geld- und Hedge-Fonds-Manager und besitzt einen MBA-Abschluss im Finanzwesen und Marketing von der Manchester University sowie einen BA in Business Management von der Fairleigh Dickinson University.',
    'teamAdvisersPartners#162': 'Er ist ein Co-CEO von Blonde 2.0, einer preisgekrnten globalen PR-Agentur, auch bekannt als einer der Movers and Shakers der Startup Nation bei Forbes. Als Co-CEO von Blonde 2.0 ist er verantwortlich fr die Public-Relations-Aspekte sowie fr Strategie- und Krisenmanagement.',
    'teamAdvisersPartners#163': 'Mit einem Team von 35 professionellen Mitarbeitern ist Blonde 2.0 ist eine der treibenden Krfte in der PR fr Blockchain- und Crypto-Start-ups und -VCs.',
    'teamAdvisersPartners#164': 'Leonard Grayver',
