    'teamAdvisersPartners#166': 'Reprsentierte ber 400 Start-ups',
    'teamAdvisersPartners#167': 'Generierte mehr als 18 Mio. USD fr sein Start-up, als er Student an der juristischen Fakultt war.',
    'teamAdvisersPartners#168': 'Leonard Grayver wurde in der UdSSR geboren und zog mit seinen Eltern als Kind in die USA. Whrend seines Jura-Studiums grndete er ein Start-up und generierte erfolgreich mehr als 18 Mio. USD fr die Finanzierung.',
    'teamAdvisersPartners#169': 'Sein Start-up hat nicht berlebt, aber seitdem hat Leonard mehr als 400 Start-ups geholfen.',
    'teamAdvisersPartners#170': 'Insgesamt hat Leonard mehr als 10 Jahre Erfahrung mit VC-Finanzierung, Fondsstrukturierung und Due Diligence (reprsentiert sowohl Fonds als auch LPs). Sein Rat an alle Unternehmer  folgen Sie nicht den Trends, folgen Sie Ihren Interessen.',

