    'teamAdvisersPartners#60': 'Fundador y presidente ejecutivo de Accelerated Digital Ventures',
    'teamAdvisersPartners#61': 'Asesor para ICOBox, Crypterium, EnergiMine, etc.',
    'teamAdvisersPartners#62': 'Accionista fundador de TechCrunch',
    'teamAdvisersPartners#63': 'Soy el presidente ejecutivo y presidente del comit de inversiones de Accelerated Digital Ventures, una empresa del Reino Unido. Nac y me cri en el Reino Unido.',
    'teamAdvisersPartners#64': 'Anteriormente, fui fundador y socio de Archimedes Labs en Palo Alto (California). Me encanta trabajar con fundadores, equipos, diseadores e ingenieros para crear productos, contar historias y hacerlas realidad.',
    'teamAdvisersPartners#65': 'He fundado y cofundado varias empresas desde principios de los 80. Algunas se vendieron con xito y otras salieron al mercado en una OPV. Dos de mis empresas fueron unicornios, valoradas en ms de mil millones de dlares.',
    'teamAdvisersPartners#66': 'Siempre me he centrado en las reas de cambio. En la dcada de los 80, fueron las redes y las bases de datos. En la poca de 1994 a 1998, fue el acceso a Internet. De 1998 a 2010 fueron los servicios y contenidos web, as como las aplicaciones mviles para el consumidor. Desde 2017, es la tecnologa de cadenas de bloques. Carrera y proyectos: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs y CINDX.',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'Director de servicios financieros internacionales',
    'teamAdvisersPartners#69': 'Ms 30 aos de experiencia en servicios financieros',
    'teamAdvisersPartners#70': 'Creacin de empresas valoradas en 500 000 000 $',
    'teamAdvisersPartners#71': 'Director general experto en servicios financieros de eficacia demostrada con especial inters en la creacin de negocios de seguros en mercados en desarrollo, donde he creado empresas con xito valoradas actualmente en ms de 500 000 000 $.',
    'teamAdvisersPartners#72': 'Ms recientemente, he trabajado en Rusia durante 10 aos y tambin he ocupado altos cargos ajenos a los seguros, como el de presidente general de Workle (la mayor plataforma de empleos comerciales de Internet), y una amplia variedad de puestos de nivel C en empresas emergentes del sector tecnofinanciero como Crypterium, el primer criptobanco digital del mundo. Carrera y proyectos:',
    'teamAdvisersPartners#73': 'Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex y CINDX.',
    'teamAdvisersPartners#74': 'Philip Staehelin',
    'teamAdvisersPartners#75': '25 aos de experiencia en asesora y emprendimiento',
    'teamAdvisersPartners#76': 'Negocios de pagos alternativos de 500 000 000 $ bajo supervisin',
    'teamAdvisersPartners#77': 'Mentor de ms de 200 empresas emergentes de 20 pases',
    'teamAdvisersPartners#78': 'Soy innovador y lder con una combinacin de optimismo contagioso y realismo pragmtico. Encuentro oportunidades invisibles para los dems.',
    'teamAdvisersPartners#79': 'He potenciado mi capacidad para "ver las cosas de otra forma" y "hacer las cosas realidad" en corporaciones, asesoras y empresas emergentes, lo que me ha aportado nuevas y amplias perspectivas que sirven de base para nuevas ideas transformadoras.',
    'teamAdvisersPartners#80': 'Buscar soluciones para grandes desafos es lo que me saca de la cama cada maana. ',
    'teamAdvisersPartners#81': 'Carrera y proyectos: Roland Berger, Dot Glasses, Transparency International, StartupYard, Bethereum, Gjirafa Inc, Accenture y CINDX.',
    'teamAdvisersPartners#82': 'Amarpreet Singh',
    'teamAdvisersPartners#83': 'Asesor experto de Global Blockchain Foundation',
    'teamAdvisersPartners#84': 'Experiencia en Microsoft, The World Bank y Airbus',
    'teamAdvisersPartners#85': 'Miembro del consejo asesor de proyectos de cadenas de bloques',
    'teamAdvisersPartners#86': 'Entusiasta de la tecnologa digital y profesional avezado con aos de experiencia en el sector tecnolgico, concretamente en operaciones, asesora e innovacin.',
    'teamAdvisersPartners#87': 'Especialista en servicios en la nube certificado (Microsoft y AWS). Conocido por su versatilidad y flexibilidad. Goza de excelentes destrezas analticas, estratgicas y de liderazgo adems de ser un compaero de primera.',
    'teamAdvisersPartners#88': 'Especialidades: asesora tecnolgica, operaciones, informtica en la nube, tecnofinanzas, innovacin, ERP, aprendizaje automtico, gestin de cuentas, desarrollo de socios, desarrollo empresarial y estrategia.',
    'teamAdvisersPartners#89': 'Amarpreet Singh cuenta con una experiencia laboral y una formacin muy diversificadas. Tiene tres ttulos de mster y ha vivido, trabajado y estudiado en India, Singapur, Francia, China, Sudfrica, Corea y Canad.',
    'teamAdvisersPartners#90': 'Carrera y proyectos: Microsoft, The World Bank, Airbus, TokenAsia Platform, GOeurika y CINDX.',
    'teamAdvisersPartners#91': 'Tyler Sanford',
    'teamAdvisersPartners#92': '8 aos de marketing digital en Yelp y Zenefits',
    'teamAdvisersPartners#93': '12 ICO con una recaudacin de ms de 225 000 000 $',
    'teamAdvisersPartners#94': 'Uno de los primeros operadores en adoptar la tecnologa de cadenas de bloques',
    'teamAdvisersPartners#95': 'Crecer como atleta me brind unas destrezas que he podido aplicar a mi carrera profesional y vida personal.',
    'teamAdvisersPartners#96': 'He tenido la gran suerte de ver los resultados de ir un paso ms lejos y, adems, he aprendido a lidiar con los altibajos inherentes a toda profesin.',
    'teamAdvisersPartners#97': 'Soy una persona muy motivada, y siempre tengo ganas de seguir ampliando mis conocimientos y talentos en todos los aspectos de los negocios.',
    'teamAdvisersPartners#98': 'Siempre dispuesto a aceptar nuevos desafos, mi motivacin se basa en superar los obstculos para alcanzar mis objetivos.',
    'teamAdvisersPartners#99': 'Carrera y proyectos: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation y CINDX.',
