    'teamAdvisersPartners#125': 'Bogdan Fiedur',
    'teamAdvisersPartners#126': 'Experto, inversor y emprendedor en cadenas de bloques y criptomonedas',
    'teamAdvisersPartners#127': 'Cofundador de bitJob y presidente de Blockchainexperts.io',
    'teamAdvisersPartners#128': 'Asesor de ICO Advisor, experto de ICOBench',
    'teamAdvisersPartners#129': 'Bogdan Fiedur es desarrollador de contratos inteligentes, criptoinversor, empresario y presidente de http://blockchainexperts.io.',
    'teamAdvisersPartners#130': 'Cuenta con ms de 20 aos de experiencia en el sector de la informtica, en la creacin de sitios web de comercio electrnico, y lleva tres aos trabajando en el desarrollo de las cadenas de bloques. Tambin es asesor de ICO y ha trabajado con 20 ICO de xito hasta la fecha.',
    'teamAdvisersPartners#131': 'Est muy involucrado en la organizacin de presentaciones y talleres sobre cadenas de bloques y programacin de contratos inteligentes en su criptocomunidad local de Winnipeg. Bogdan est centrado actualmente en el sector de las cadenas de bloques y ofrece asesoramiento, servicios de consultora y contactos a los expertos en la materia. Bogdan ha cofundado recientemente Bitjob, una plataforma de cadenas de bloques para autnomos dirigida a estudiantes, y ha participado en varios proyectos de ICO, como PayPie.com.',
    'teamAdvisersPartners#132': 'Carrera y proyectos: US Blockchain Association, Tuurnt, Trends Project, BiNeuro, Zichain, Graphen Tech, CyClean, Swachhcoin y CINDX.',
    'teamAdvisersPartners#133': 'Graham Doggart',
    'teamAdvisersPartners#134': '20 aos de desarrollo empresarial y cumplimiento de marketing relativo a tecnofinanzas',
    'teamAdvisersPartners#135': 'Ejecucin de campaas por valor de 100 000 000 $',
    'teamAdvisersPartners#136': 'Asesor en ms de 10 proyectos de cadenas de bloques este ao',
    'teamAdvisersPartners#137': 'Soy presidente de un proyecto de criptomonedas oculto adems de cofundador y jefe de estrategia de Dynamic Abundance, una asesora especializada en tecnologa de cadenas de bloques y tecnofinanzas, desde 2012.',
