    'teamAdvisersPartners#62': 'Fundador e acionista da TechCrunch',
    'teamAdvisersPartners#63': 'Sou o Presidente Executivo e Presidente do Comit de Investimento na Accelerated Digital Ventures - uma empresa de capital de risco no Reino Unido. Sou nascido e criado no Reino Unido.',
    'teamAdvisersPartners#64': 'Anteriormente, fui fundador e scio na Archimedes Labs em Palo Alto, na Califrnia. Adoro trabalhar com fundadores, equipas, designers e engenheiros para criar produtos, contar histrias e transform-los em realidade. Fui fundador e cofundador de muitas empresas, no incio dos anos 80. Algumas foram vendidas com sucesso, outras sujeitas a OPA.',
    'teamAdvisersPartners#65': 'Duas das minhas empresas eram "unicrnios" - avaliadas por outros em mais de mil milhes $. Sempre me foquei no ponto de mudana. Nos anos 80, redes e bases de dados. No perodo entre 1994 e 1998, acesso  Internet. Entre 1998 e 2010 foram os Servios e Contedos de Internet, Aplicaes de Consumo Mveis. Desde 2017, a rea de Blockchain.',
    'teamAdvisersPartners#66': 'Carreira e projetos: TechCrunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs e CINDX',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'Diretor Executivo na International Financial Services',
    'teamAdvisersPartners#69': '+30 anos de experincia em servios financeiros',
    'teamAdvisersPartners#70': 'Criou empresas com um valor de 500 milhes $',
    'teamAdvisersPartners#71': 'Diretor Executivo experiente com resultados provados em servios financeiros, com enfoque particular na criao de negcios de seguros em mercados em desenvolvimento, onde criei empresas de sucesso com uma avaliao atual superior a 500 milhes $',
    'teamAdvisersPartners#72': ' A experincia mais recente  na Rssia (10 anos), assim como outros cargos na gesto snior em reas no afetas aos seguros, incluindo Diretor Executivo da maior plataforma de emprego digital da Rssia, a Workle, e uma grande variedade de cargos de nvel C em startups de fintech, incluindo o Crypterium, o primeiro criptobanco digital do mundo.',
    'teamAdvisersPartners#73': 'Carreira e projetos: Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex, CINDX',
    'teamAdvisersPartners#74': 'Philip Staehelin',
